import React, { useEffect, useState } from 'react';
import Airtable from 'airtable';

import './Experience.css';
import './components/Role.css';

import experience_bullet from "./assets/images/experience_bullet.png";

const base = new Airtable({apiKey: 'keypdEsruXrumSCbV'}).base('app9PStCe8EWT2PuX');

function Experience() {

  const [experiences, setExperiences] = React.useState([]);

  React.useEffect(() => {
    base('Experience')
      .select({view: 'Grid view' })
      .eachPage((records, fetchNextPage) => {
        setExperiences(records);
        fetchNextPage(); // prob not necessary
      })
  }, []);
  
  return (
    
    <div className="Experience">
      
      {/* <div className="experience_title">some experiences:</div> */}
        {/* <div className="roles">
          {experiences.map((exp) => (
              <Role 
                date={exp.fields.date}
                position={exp.fields.position}
                bullet={exp.fields.bullet}
                />
            ))}
        </div> */}
        <div className="roles2">
          <table id="roles">
            <tr className="border_bottom">
              <th>
              my experience:
              </th>
            </tr>
            {experiences.map((exp) => (
                <div>
                  <tr className="date_row">
                    <td></td>
                    <td>
                      {exp.fields.date}
                    </td>
                  </tr>
                  <tr className="border_bottom">
                    <td className="bullet_cell">
                      <img className="bullet_img" src={experience_bullet} alt="star bullet"></img>
                    </td>
                    <td className="position_cell">
                        {exp.fields.position} 
                    </td>
                  </tr>
                </div>
            ))}
          </table>
        </div>
    </div>
  );
}



export default Experience;