import './AboutMe.css';
import React, { useEffect, useState } from 'react';
import Airtable from 'airtable';

import Loved from './components/Loved.js';
import monet from "./assets/images/monet.png";
import grandmas_store from "./assets/images/grandmas_store.png";
import portland from "./assets/images/portland.jpg";


const base = new Airtable({apiKey: 'keypdEsruXrumSCbV'}).base('app9PStCe8EWT2PuX');

// to do: replace the things inside <div className>'s with Loved.js components
// sep '21 update: delete Loved.js component
function AboutMe() {

  const [thingsilove, setThingsILove] = React.useState([]);

  React.useEffect(() => {
    base('Things I Love')
      .select({view: 'Grid view' })
      .eachPage((records, fetchNextPage) => {
        setThingsILove(records);
        fetchNextPage(); // prob not necessary
      })
  }, []);

  const loveArray = [];
  thingsilove.map((thingilove) => loveArray.push(thingilove.fields.thing));

  return (
    <div className="AboutMe">
      <div className="aboutme_container">
        <div className="aboutme_title">
          <div className="star_enjoy">*</div>
          <br />
          <br />
          things i love...
        </div>
        <div className="love_container">
          <div>
            <img className="love" src={portland} alt="these"></img>
            <div className="caption">{loveArray[0]}</div>
          </div>
          <div>
            <img className="love" src={grandmas_store} alt="constantly"></img>
            <div className="caption">{loveArray[1]}</div>
          </div>
          <div>
            <img className="love" src={monet} alt="change!"></img>
            <div className="caption">{loveArray[2]}</div>
          </div>
        </div>
        
      </div>
    </div>
  );
}

export default AboutMe;