import React, { useState, useEffect } from 'react';
import Airtable from 'airtable';
import './App.css';
import AboutMe from './AboutMe.js';
import './AboutMe.css';
import Experience from './Experience.js';
import './Experience.css';
import Projects from './Projects.js';
import Goodbye from './Goodbye.js';
import Greeting from './Greeting.js';

const base = new Airtable({apiKey: 'keypdEsruXrumSCbV'}).base('app9PStCe8EWT2PuX');

function App() {

  const [segwayText, setSegwayText] = React.useState([]);

    React.useEffect(() => {
        base('Segway')
        .select({view: 'Grid view' })
        .eachPage((records, fetchNextPage) => {
          setSegwayText(records);
            fetchNextPage(); // prob not necessary
        })
    }, []);
    
    const segwayTextArray = [];
    segwayText.map((segway) => segwayTextArray.push(segway.fields.segways));

  return (
    <div className="App">
      <div className="chinese-name">朱<br/>美<br/>华</div>
      <Greeting />

      <div className="segway">
                    <div className="segway_star">*</div>
                    &nbsp;{segwayTextArray[0]}&nbsp;
                    <div className="segway_star">*</div>
                </div>
      <Experience />
      <AboutMe />
      <Projects />
      <div className="goodbye_segway">
        <div className="segway_star">*</div>
        &nbsp;{segwayTextArray[1]}&nbsp;
        <div className="segway_star">*</div>
      </div>
      <Goodbye />
    </div>
  );
}

export default App;
